.header {
  position: relative;
  z-index: 999;
  height: 150px;
  width: 100%;
  margin-right: auto!important;
  margin-left: auto!important;
  /*background-color: #81D8D0;*/
}

.brand-container {
  display: flex;
  text-align: center;
  height: 100px;
  /* line-height: 100px; */
}

.brand-image {
  position: relative;
  width: 100%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  /* border-bottom: seagreen 1px solid; */
}

.brand {
  width: 50%;
  font-size: 60px;
  letter-spacing: 4px;
  color: black;
  text-decoration: none;
  line-height: 100px;
}

.urls {
  background-color: #81D8D0;
  height: 50px;
}

.urls-control-panel {
  background-color: rgb(241, 117, 117);
  height: 50px;
}

.urls-container {
  position: relative;
  display: flex;
  left: 50%;
  height: 100%;
  width: 60%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.url {
  color: #fff;
  line-height: 30px;
  text-decoration: none;
  position: relative;
  width: 25%;
  top: 50%;
  height: 30px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.url-box {
  position: relative;
  top: 50%;
  height: 30px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: center;
}

.url-box-border {
  border-right: #fff 3px solid;
}

.url-box:hover {
  background-color: #fff;
  color: #81D8D0;
}

.url-box-control-panel:hover {
  background-color: #fff;
  color: rgb(241, 117, 117);
}



.main {
  position: relative;
  width: 100%;
  height: 100%;
}

.footer {
  position: relative;
  bottom: 0px;
  height: 10px;
  width: 100%;
  padding: 25px 0px;
  background-color: #EAEEF1;
  /*box-shadow: 0px -1px 0px rgba(1,1,1,.8);*/
}

.footer .content {
  line-height: 10px;
  color: rgba(1,1,1,.4);
  width: 90%;
  display: flex;
  margin-right: auto!important;
  margin-left: auto!important;
}

.footer .content .copyright {
  width: 60%;
}

.footer-item {
  width: 8%;
  text-align: center;
}

.footer-item-shadow {
  box-shadow: 2px 0px 0px rgba(1,1,1,.4); 
}

.fkClick {
  position: absolute;
  right: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  border: none;
  background: transparent;
  outline: none;
  cursor: pointer;
  text-indent: -10px;
  z-index: 999;
}

.fkClick:hover ~ .url-box {
  background-color: #fff;
  color: #81D8D0;
}

.fkClick:focus ~ .dropDown {
  visibility: visible;
  opacity: 1;
}

.dropDown {
  visibility: hidden;
  z-index: 999;
  opacity: 0;
  position: absolute;
  width: 100%;
  text-align: center;
  transition: all .5s ease;
  top: 40px;
}

.dropDown-main {
  position: relative;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  background-color: #f3f3f3;
}

.dropDown-main a {
  color: rgba(1,1,1,.5);
  text-decoration: none;
  line-height: 50px;
}

.dropDown-item {
  
}

.dropDown-item:hover {
  background-color: rgba(1,1,1,.3);
}
